.btn-contact {
  text-decoration: none;
  color: $color-2;
  box-shadow: 2px 4px 4px $color-5;
  display: inline-block;
  padding: .75rem 1.25rem;
  border-radius: 10rem;
  font-size: 1rem;
  letter-spacing: .15rem;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  z-index: 1;

  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-3;
    border-radius: 10rem;
    z-index: -2;
  }

  &:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: darken($color-3, 15%);
    transition: all .3s;
    border-radius: 10rem;
    z-index: -1;
  }

  &:hover {
    color: #fff;

    &:before {
      width: 100%;
    }
  }

  @media (min-width: 787px) {
    .btn-contact {
      width: 285px;
      height: 51px;
      font-size: 23px;
      display: flex;
      justify-content: center;
    }

    .btn-contact:hover {
      color: $color-1;
      background-color: $color-4;
    }
  }
}